@import "helpers/mixins";

.charity-page {
  .char_head {
    margin: 10rem auto;
    .txt {
      text-align: center;
      z-index: 2;
      position: relative;
      background: {
        image: url("../assets/images/office_content/naa islamophopia area-01.webp");
        size: contain;
        repeat: no-repeat;
        position: right;
      }
      @include breakpoints(min-small) {
        padding: 6rem;
      }
      @include breakpoints(min-xlarge) {
        margin-left: 40%;
        width: 40vw;
      }

      & > * {
        @include breakpoints(min-xlarge) {
          transform: translateX(-70px);
        }
      }

      h2 {
        margin-bottom: 1.7rem;
      }

      a {
        position: absolute;
        bottom: 0%;
        left: 50%;
        transform: translate(-50%, 100%);

        button {
          background-color: var(--bg-clr);

          &:hover {
            background-color: white;
          }
        }
      }
    }
  }

  .aims {
    @media (min-width: 1900px) {
      padding-top: 10rem;
    }
  }

  .mission {
    .missions {
      margin-top: 3rem;
      display: flex;
      flex-wrap: wrap;
      flex-direction: column;
      gap: 4rem;

      @include breakpoints(min-xlarge) {
        flex-direction: row;
      }

      .msn {
        flex: 1;
        position: relative;
        padding: 1.3rem 2.5rem 5rem;
        border-bottom-right-radius: 0;

        h2 {
          letter-spacing: 30px;
          width: fit-content;
          margin-bottom: 2rem;
          font-style: italic;

          @include breakpoints(min-small) {
            transform: translateX(-50%);
          }
        }

        &:nth-child(1) {
          border-top-right-radius: calc(min(30vw, 10rem));
        }

        &:nth-child(2) {
          border-bottom-right-radius: calc(
            min(30vw, 10rem)
          );
        }

        &:nth-child(3) {
          border-top-left-radius: calc(min(30vw, 10rem));
        }

        a {
          right: auto;
          left: 20%;
          button {
            padding: 0.33rem 3rem;
          }
        }
      }
    }
  }

  .also,
  .honour {
    margin: 8rem auto;
    .swp {
      margin-top: 3rem;

      .sweeper .swiper .swiper-wrapper .swiper-slide img {
        aspect-ratio: 0.8;
        object-fit: contain;
        max-width: unset;
      }
    }
  }

  .foodBox {
    @include breakpoints(mobile) {
      padding-top: 2rem;
      overflow: hidden;
    }
  }

  section.events {
    .eventsCont {
      margin-top: 2rem;
      display: flex;
      flex-direction: column;
      gap: 8rem;

      .event {
        gap: calc(clamp(2rem, 2vw, 5rem));

        @include breakpoints(max-medium) {
          flex-direction: column-reverse;
        }

        &:nth-child(even) {
          flex-direction: row-reverse;
          .coloredTxtBox {
            border-top-left-radius: calc(min(30vw, 10rem));
            border-bottom-right-radius: unset;
            padding-left: calc(min(15vw, 8rem));
          }
          @include breakpoints(max-medium) {
            flex-direction: column-reverse;
          }

          .text {
            border-top-left-radius: calc(min(30vw, 10rem));
            border-bottom-right-radius: 0;
            animation: up-Down 1s ease-in-out infinite
              alternate;

            @include breakpoints(min-medium) {
              padding-left: 8rem;
            }

            a {
              right: 20%;
            }
          }
        }

        .vid {
          flex: 30%;

          img {
            display: block;
            margin: auto;
          }
        }
      }
    }
  }

  section.reviews,
  .aims {
    .reviewsCont {
      margin-top: 3rem;
      display: flex;
      flex-direction: column;
      gap: calc(max(3vw, 3rem));
      .review {
        animation: up-Down 1s ease-in-out infinite alternate;
        padding: 2.5rem 1.5rem;
        &:nth-child(2) {
          animation-delay: 0.3s;
        }
        &:nth-child(3) {
          animation-delay: 0.6s;
        }
        &:nth-child(4) {
          animation-delay: 0.9s;
        }

        @include breakpoints(min-xlarge) {
          max-width: 70%;
        }

        &:nth-child(even) {
          border-top-left-radius: calc(min(30vw, 10rem));
          border-bottom-right-radius: 0;

          @include breakpoints(min-xlarge) {
            align-self: flex-end;
          }

          @include breakpoints(min-medium) {
            padding-left: 8rem;
          }
          @include breakpoints(mobile) {
            padding-left: 5rem;
          }

          a {
            right: 20%;
          }
        }
      }
    }
  }

  section.dontMsgs {
    .msgs {
      background: {
        image: url("../assets/images/donate/background-01.webp");
        size: 60%;
        repeat: no-repeat;
        position: right bottom;
      }
    }
  }

  .festiv {
    min-height: 50vh;

    background: {
      image: url("../assets/images/office_content/choose right way-01.webp");
      size: contain;
      position: center;
      attachment: fixed;
      repeat: no-repeat;
    }
  }
}

section {
  margin: 5rem auto;
}

ul {
  list-style: unset;
  padding-left: 3rem;
}
section.reviews,
section.dontMsgs {
  .swp {
    .narrowSwiper {
      img {
        max-width: unset !important;
        aspect-ratio: 1;
        width: calc(min(25vw, 300px));
        object-fit: cover;
      }
    }
  }
}
.withBg::before {
  transform: translate(-45%, -29%) rotate(80deg);
}

.coloredTxtBox {
  background-color: var(--bg-clr);
  padding: 1.3rem 1.5rem 3rem;
  border-bottom-right-radius: calc(min(30vw, 10rem));
  box-shadow: -5px 10px 15px grey;
  position: relative;

  p.bold {
    margin-bottom: 1rem;
  }

  a {
    position: absolute;
    bottom: 0;
    right: 25%;

    @include breakpoints(min-small) {
      right: 60%;
    }
    button {
      background-color: var(--alt-clr);
      padding: 0.33rem 4rem;
      border-radius: 40px 0;
      box-shadow: -3px 5px 15px grey;
      transform: translateY(50%);

      &:hover {
        background-color: white;
        filter: drop-shadow(0 0 10px var(--alt-clr));
      }
    }
  }
}

.withBg.pos2::before {
  transform: translate(-45%, -40%) rotate(153deg);
}

body.arabic {
  text-align: right;

  ul {
    padding-left: unset;
    padding-right: 3rem;
  }

  .char_head {
    .txt {
      margin: auto;

      background-position: center;
    }
  }

  .foodBox .text,
  .aboutCha,
  section.events .eventsCont .event .text,
  .reviews .review,
  .reviews h2,
  .reviews p {
    direction: rtl;
  }

  .foodBox .withBg::before {
    transform: translate(15%, -25%) rotate(-210deg) !important;
  }

  section.events .eventsCont .event {
    &:nth-child(odd) .coloredTxtBox {
      @include breakpoints(min-medium) {
        padding-right: 8rem;
      }
    }

    &:nth-child(even) .coloredTxtBox {
      @include breakpoints(min-medium) {
        padding-right: 3rem;
      }
    }
  }

  .reviewsCont {
    .review {
      @include breakpoints(min-medium) {
        padding-right: 8rem !important;
      }
    }
  }

  .mission .missions .msn h2 {
    letter-spacing: 0ch;
  }
}

.pubHead {
  .content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 4vw;
    position: relative;

    @include breakpoints(mobile) {
      gap: 8vw;
      flex-direction: column !important ;
    }

    .text {
      flex-basis: 65%;
      h2 {
        font-size: 1.7rem;
        margin-bottom: 3.5vh;
        @include breakpoints(min-xlarge) {
          font-size: 2.5rem;
        }
      }

      p {
        &:not(:last-child) {
          margin-bottom: 2.5vh;
        }
      }
    }

    .poster {
      flex-basis: 35%;
      position: relative;

      @include breakpoints(mobile) {
        order: -1;
      }
      img {
        border-radius: 100px 0;
        filter: drop-shadow(4px 5px 10px grey);
        animation: up-Down 1.2s ease-in-out infinite
          alternate;
        position: unset !important;
        max-width: 100% !important;

        &:hover {
          filter: drop-shadow(
            0 0.2rem 0.25rem var(--alt-clr)
          );
          transform: scale(1.1);
        }

        @include breakpoints(mobile) {
          max-width: 50vw !important;
        }
      }
    }
  }
}

.pubSwiper {
  overflow: unset;

  .swiper-wrapper .swiper-slide {
    height: unset !important;
    width: fit-content !important;
  }

  .swiper-button-next-unique,
  .swiper-button-prev-unique {
    position: absolute;
    top: 50%;
    width: calc(var(--swiper-navigation-size) / 44 * 27);
    height: var(--swiper-navigation-size);
    margin-top: calc(
      0px - (var(--swiper-navigation-size) / 2)
    );
    z-index: 10;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(
      --swiper-navigation-color,
      var(--swiper-theme-color)
    );
  }
  .swiper-button-next-unique,
  .swiper-rtl .swiper-button-prev-unique,
  .swiper-button-prev-unique,
  .swiper-rtl .swiper-button-next-unique {
    position: absolute;
    top: 50%;
    right: -30%;
    transform: translate(-50%, -50%);

    @media (min-width: 1800px) {
      right: -15%;
    }
  }
  .swiper-button-next-unique:after,
  .swiper-button-prev-unique:after {
    font-family: swiper-icons;
    font-size: var(--swiper-navigation-size);
    text-transform: none !important;
    letter-spacing: 0;
    font-variant: initial;
    line-height: 1;
    color: var(--alt-clr);
  }
  .swiper-button-next-unique:after,
  .swiper-rtl .swiper-button-prev-unique:after {
    content: "next";
  }
  .swiper-button-prev-unique,
  .swiper-rtl .swiper-button-next-unique {
    left: -20%;
    right: auto;

    @media (min-width: 1800px) {
      left: -10%;
    }
  }
  .swiper-button-next-unique.swiper-button-disabled,
  .swiper-button-prev-unique.swiper-button-disabled {
    opacity: 0.35;
    cursor: auto;
    pointer-events: none;
  }
  .swiper-button-prev-unique:after,
  .swiper-rtl .swiper-button-next-unique:after {
    content: "prev";
  }
}

footer {
  margin-top: 5rem;
}
